.carousel {
  height: 370px;
  display: block;
  margin: 0;
  position: relative;
  box-shadow: $level-1;
  filter: brightness(1);
  overflow: hidden;

  &--leftChevron {
    position: relative;
    left: -2px;
    opacity: 0.8;
  }
  &--rightChevron {
    position: relative;
    right: -1px;
    opacity: 0.8;
  }

  &--decrement {
    display: none;
  }
  &--increment {
    display: none;
  }

  &--slider {
    &-enter {
      transform: translateX(100vw);
    }
    &-enter-active {
      transform: translateX(0);
      transition: transform 600ms ease-in;
    }
    &-exit {
      transform: translateX(0);
    }
    &-exit-active {
      transform: translateX(-100vw);
      transition: transform 600ms ease-in;
    }
  }

  &--location-chip {
    height: 6px;
    width: 35px;
    background-color: rgba(255, 255, 255, 0.35);
    display: block;
    border: 1px solid rgba(0, 0, 0, 0.4);
  }
  &--location {
    position: absolute;
    width: 100%;
    z-index: 600;
    bottom: 40px;
  }
  &--container {
    position: relative;
    width: 200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 6px;
  }
  &--slider {
    width: 100%;
    height: 550px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
  }
  &--text-container {
    max-width: 1150px;
    margin: 0 auto;
    height: 550px;
    padding: 0 12px;
  }
  &--dark {
    color: #303743;
  }
  &--light {
    color: white;
  }

  .left-button {
    display: none;
    position: absolute;
    left: 0;
    top: 190px;
    width: 65px;
    height: 75px;
    background-color: $isabelline;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-image: url(../../images/carousel/left-light.svg);
    background-repeat: no-repeat;
    background-position: center;
    left: -1px;
    border-top: 1px solid #cecece;
    border-right: 1px solid #cecece;
    border-bottom: 1px solid #cecece;
  }

  .right-button {
    display: none;
    position: absolute;
    right: 0;
    top: 190px;
    width: 65px;
    height: 75px;
    background-color: $isabelline;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-image: url(../../images/carousel/right-light.svg);
    background-repeat: no-repeat;
    background-position: center;
    right: -1px;
    border-top: 1px solid #cecece;
    border-left: 1px solid #cecece;
    border-bottom: 1px solid #cecece;
  }
}

@media only screen and (min-width: 600px) {
  .carousel {
    height: 500px;

    &--decrement {
      position: absolute;
      left: 24px;
      top: 250px;
      background-color: rgba(255, 255, 255, 0.5);
      border: 1px solid rgba(255, 255, 255, 0.7);
      height: 50px;
      width: 50px;
      border-radius: 50%;
      z-index: 60;
      display: grid;
      justify-items: center;
      align-items: center;
      color: #000;
      cursor: pointer;
      box-shadow: 0 1px 0px rgba(0, 0, 0, 0.03), 0 1px 1px rgba(0, 0, 0, 0.05);
    }
    &--increment {
      position: absolute;
      right: 24px;
      top: 250px;
      background-color: rgba(255, 255, 255, 0.5);
      border: 1px solid rgba(255, 255, 255, 0.7);
      height: 50px;
      width: 50px;
      border-radius: 50%;
      z-index: 60;
      display: grid;
      justify-items: center;
      align-items: center;
      color: #000;
      cursor: pointer;
      box-shadow: 0 1px 0px rgba(0, 0, 0, 0.03), 0 1px 1px rgba(0, 0, 0, 0.05);
    }
  }
}

@media (prefers-color-scheme: dark) {
  .carousel {
    border: 1px solid #000;
    .left-button {
      background-color: $dark-lava;
      background-image: url(../../images/carousel/left-dark.svg);
      border-top: 1px solid #000;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
    }
    .right-button {
      background-color: $dark-lava;
      background-image: url(../../images/carousel/right-dark.svg);
      border-top: 1px solid #000;
      border-left: 1px solid #000;
      border-bottom: 1px solid #000;
    }
  }
}

@media (prefers-color-scheme: light) {
  .carousel {
    border: 1px solid #cecece;
    // filter: brightness(1);
    .left-button {
      background-color: $isabelline;
      background-image: url(../../images/carousel/left-light.svg);
      border-top: 1px solid #cecece;
      border-right: 1px solid #cecece;
      border-bottom: 1px solid #cecece;
    }
    .right-button {
      background-color: $isabelline;
      background-image: url(../../images/carousel/right-light.svg);
      border-top: 1px solid #cecece;
      border-left: 1px solid #cecece;
      border-bottom: 1px solid #cecece;
    }
  }
}
